.clarifiction {
  z-index: 10;

  @include zero;
  flex-basis: 50%;
  padding-top: 25px;

  @include basic-font;
  text-align: right;
  color: $darkest-grey;

  @media (max-width: $min-tablet - 1) {
    display: none;
  }

  @media (min-width: $min-desktop) {
    margin-left: 50%;
    margin-top: -75px;
  }
}
