.contacts {
  position: relative;
}

.container--contacts {
  @media (min-width: $min-desktop) {
    display: flex;
    flex-wrap: wrap;
    width: $min-desktop;
    padding-left: 224px;
    padding-right: 50px;
    padding-bottom: 47px;
    margin: 0 auto;
  }
}

.container--contacts-min {
  @media (min-width: $min-desktop) {
    flex-basis: 350px;

    @include zero;
  }
}

.contacts__title {
  position: relative;

  @include zero;
  @include weight-font(30px, 18px);
  padding-bottom: 27px;

  @media (min-width: $min-tablet) {
    padding-bottom: 48px;

    @include weight-font(50px, 43px);
  }

  @media (min-width: $min-desktop) {
    @include weight-font(57px, 53px);
    padding-top: 82px;
    padding-bottom: 35px;
  }
}

.contacts__title::before {
  @media (min-width: $min-tablet) {
    content: "";
    position: absolute;
    right: 3px;
    top: -22px;
    width: 47px;
    height: 87px;
    background-image: url("../img/icon-flag.svg");
    background-repeat: no-repeat;
  }

  @media (min-width: $min-desktop) {
    top: 65px;
    left: -111px;
  }
}

.contacts__item {
  @include zero;
  @include basic-font(30px);

  @media (min-width: $min-tablet) {
    @include basic-font(30px, 17px);
  }
}

.contacts__map-area {
  width: 527px;
  flex-basis: 526px;
  padding-bottom: 0;
}

.contacts__map {
  display: block;
  margin: 0 auto;
  padding-top: 5px;
  padding-bottom: 39px;

  @media (min-width: $min-tablet) {
    width: 768px;
    padding-bottom: 55px;
    margin: 0 auto;
  }

  @media (min-width: $min-desktop) {
    width: 527px;
    flex-basis: 526px;
    padding-top: 2px;
    padding-bottom: 0;
  }
}

.contacts__write-us {
  @media (min-width: $min-tablet) {
    max-width: 230px;
  }

  @media (min-width: $min-desktop) {
    margin-left: 0;
    margin-top: -52px;
  }
}

.contacts__mail {
  padding-bottom: 2px;
  color: inherit;
  text-decoration: none;
  border-bottom: solid 1px $border-mint;

  @media (min-width: $min-tablet) {
    padding-bottom: 5px;
  }

  @media (min-width: $min-desktop) {
    padding-bottom: 8px;
  }
}

.contacts__mail:hover,
.contacts__mail:focus {
  color: $hover-dark-mint;
}

.contacts__mail:active {
  border-bottom: none;
}
